import {
  Box,
  Flex,
  IconButton,
  Stack,
  Button,
  useDisclosure,
  useBreakpointValue,
} from "@chakra-ui/react";
import { MdMenu, MdClose } from "react-icons/md"; // Import React Icons

const Header = () => {
  const { isOpen, onToggle } = useDisclosure();
  const display = useBreakpointValue({ base: "block", md: "none" }); // Show Hamburger icon only on mobile

  return (
    <Box as="header" bg="teal.500" color="white" p={4}>
      <Flex justify="space-between" align="center">
        {/* Logo */}
        <Box fontSize="xl" fontWeight="bold">
          My Logo
        </Box>

        {/* Mobile Hamburger Icon */}
        <IconButton
          icon={isOpen ? <MdClose /> : <MdMenu />} // Use React Icons for Hamburger and Close icon
          onClick={onToggle}
          display={display}
          aria-label="Toggle Navigation"
          variant="outline"
          colorScheme="white"
        />

        {/* Desktop Menu */}
        <Stack
          direction="row"
          spacing={4}
          display={{ base: "none", md: "flex" }} // Hide on mobile
          align="center"
        >
          <Button variant="link" color="white">
            Home
          </Button>
          <Button variant="link" color="white">
            About
          </Button>
          <Button variant="link" color="white">
            Services
          </Button>
          <Button variant="link" color="white">
            Contact
          </Button>
        </Stack>
      </Flex>

      {/* Mobile Menu (Manually controlled visibility) */}
      <Box
        display={isOpen ? "block" : "none"} // Show/hide based on isOpen state
        bg="teal.600"
        p={4}
        borderRadius="md"
        boxShadow="md"
        mt={4}
      >
        <Stack spacing={4} align="center">
          <Button variant="link" color="white">
            Home
          </Button>
          <Button variant="link" color="white">
            About
          </Button>
          <Button variant="link" color="white">
            Services
          </Button>
          <Button variant="link" color="white">
            Contact
          </Button>
        </Stack>
      </Box>
    </Box>
  );
};

export default Header;
